import flags from "../img/flags.json";

const Answer = (props) => {
  return (
    <div className="answer">
      <div className="country-desc">
        <p className="country-name">{props.selectedCountry}</p>
        {props.countryHovered !== "" && (
          <img
            className="flag"
            src={flags[props.selectedCountryCode].image}
            alt={`${props.selectedCountry} flag`}
          />
        )}
      </div>
      {props.capitalCity !== "" && (
        <p className="capital-city-name">
          Capital City: {props.capitalCity.city}
        </p>
      )}
    </div>
  );
};

export default Answer;
