import { useState, useEffect } from "react";

const World = (props) => {
  const width = 1009.6727;
  const height = 665.96301;
  const center = [width / 2, height / 2];

  const [zoom, setZoom] = useState(1.4);
  const [offset, setOffset] = useState(undefined);
  const [offsetOn, setOffsetOn] = useState(false);

  useEffect(() => {
    if (props.mapZoom !== zoom) {
      setZoom(props.mapZoom);
    }
    if (props.countryCenter.length > 0 && !offsetOn) {
      let [x, y] = props.countryCenter;
      setOffset([
        ((center[0] - x) / width) * 100,
        ((center[1] - y) / height) * 100,
      ]);
      setOffsetOn(true);
    }

    if (props.countryCenter.length === 0 && offsetOn) {
      setOffset(undefined);
      setOffsetOn(false);
    }
  }, [props.mapZoom, props.countryCenter]);

  return (
    <svg
      style={
        offsetOn
          ? {
              transform: `translateX(${
                -40 + offset[0] - (zoom - 3) * 1.6
              }%) translateY(${-50 + offset[1]}%)`,
            }
          : { transform: "translateX(-30%) translateY(-50%)" }
      }
      xmlns="http://www.w3.org/2000/svg"
      // geoView="-169.110266 83.600842 190.486279 -58.508473"
      viewBox="0 0 1009.6727 665.96301"
      width={width * zoom}
      height={height * zoom}
      className="world"
    >
      {props.children}
    </svg>
  );
};

export default World;
