import Title from "./Title";
import ISO from "../ISO.json";
import cities from "../worldcities.json";
import { useState, useEffect } from "react";
import QuestionBox from "./QuestionBox";

const types = [0, 1];
const allCountries = Object.keys(ISO);

const getRandom = (array) => {
  return array[Math.floor(Math.random() * array.length)];
};

const questionForm = ["What is the capital city of ", "In which country is "];

const Question = (props) => {
  const [questionType, setQuestionType] = useState(undefined);
  const [country, setCountry] = useState("");
  const [city, setCity] = useState("");
  const [questionReady, setQuestionReady] = useState(false);
  const [question, setQuestion] = useState("");
  const [options, setOptions] = useState([]);
  const [correctAnswer, setCorrectAnswer] = useState("");
  const [answerClicked, setAnswerClicked] = useState(false);

  const handleClick = () => {
    setAnswerClicked(true);
    props.handleSelectAnswer(country, city);
  };

  const handleReset = () => {
    setQuestionReady(false);
    props.handleResetQuestion();
  };

  // country test

  allCountries.forEach((code) => {
    let capCity = cities.filter(
      (cityObj) => cityObj.iso2 === code && cityObj.capital === "primary"
    );

    if (capCity.length === 0) {
      console.log("cap city missing", code, ISO[code]);
    }
  });

  useEffect(() => {
    if (!questionReady) {
      const qType = getRandom(types);
      setQuestionType(qType);
      let countryCode = getRandom(allCountries);
      while (!ISO[countryCode]) {
        countryCode = getRandom(allCountries);
      }
      setCountry(countryCode);
      let city = cities.filter(
        (cityObj) =>
          cityObj.iso2 === countryCode && cityObj.capital === "primary"
      )[0];
      while (city === undefined) {
        city = cities.filter(
          (cityObj) =>
            cityObj.iso2 === countryCode && cityObj.capital === "primary"
        )[0];
      }
      city = city.city;
      setCity(city);
      if (qType === 0) {
        setQuestion(questionForm[qType] + ISO[countryCode] + "?");
      } else {
        setQuestion(questionForm[qType] + city + " the capital city?");
        if (city === "None") {
          setQuestion("Which country does not have a capital city?");
        }
      }
      let availableOptions = [];

      if (qType === 1) {
        availableOptions.push(ISO[countryCode]);
        setCorrectAnswer(ISO[countryCode]);
        while (availableOptions.length < 4) {
          let newOption = ISO[getRandom(allCountries)];
          if (!availableOptions.includes(newOption)) {
            availableOptions.push(newOption);
          }
        }
        availableOptions.sort((a, b) => Math.random() - 0.5);
        setOptions(availableOptions);
      } else {
        availableOptions.push(city);
        setCorrectAnswer(city);
        const availableCities = cities
          .filter((city) => city.capital === "primary")
          .map((obj) => obj.city);
        while (availableOptions.length < 4) {
          const newCity = getRandom(availableCities);
          if (!availableOptions.includes(newCity)) {
            availableOptions.push(newCity);
          }
        }
        availableOptions.sort((a, b) => Math.random() - 0.5);
        setOptions(availableOptions);
      }

      setQuestionReady(true);
      setAnswerClicked(false);
    }
  }, [questionReady]);

  return (
    <div className="question">
      <Title />
      <QuestionBox question={question} />
      <div className="options-wrapper">
        {options.map((option, index) => {
          return (
            <div
              className={`option ${correctAnswer === option && "correct"}`}
              key={index}
              onClick={!answerClicked ? handleClick : undefined}
              style={
                answerClicked && correctAnswer === option
                  ? {
                      background:
                        "linear-gradient(to top left, rgb(113, 226, 122), rgb(137, 240, 171))",
                    }
                  : {}
              }
            >
              <p className="written-option no-select">{option}</p>
            </div>
          );
        })}
      </div>
      {answerClicked && (
        <button className="reset-btn" onClick={handleReset}>
          Next question
        </button>
      )}
    </div>
  );
};

export default Question;
