import "./App.css";
import World from "./components/World";
import ISO from "./ISO.json";
import paths from "./paths";
import Path from "./components/Path";
import CirclePath from "./components/CirclePath";
import { useState } from "react";
import cities from "./worldcities.json";
import Answer from "./components/Answer";
import Question from "./components/Question";

const findCountryCenter = (id) => {
  let pathsArr = paths.filter((path) => path.id === id);

  let output = [];

  pathsArr.forEach((path) => {
    let d = path.d;
    d = d.replace("m ", "").replace(/ z m /g, " ").replace(" z", "");

    let xArr = [],
      yArr = [];

    let currentX = 0,
      currentY = 0;

    const coordArray = d.split(" ");
    coordArray.forEach((xy) => {
      const [x, y] = xy.split(",").map((val) => +val);
      currentX += x;
      currentY += y;
      xArr.push(currentX);
      yArr.push(currentY);
    });

    for (let i = 0; i < xArr.length; i++) {
      output.push([xArr[i], yArr[i]]);
    }
  });

  const returnAvg = (array, index) => {
    return array.reduce((a, b) => a + b[index], 0) / array.length;
  };

  const center = [returnAvg(output, 0), returnAvg(output, 1)];
  const size = Math.abs(center[0] - output[0][0] + (center[1] - output[0][1]));
  const zoomSize = Math.max(3, 6 - size);
  console.log("zoomSize", id, ISO[id], zoomSize);

  return [center, zoomSize];
};

function App() {
  const [selectedCountry, setSelectedCountry] = useState("");
  const [selectedCountryCode, setSelectedCountryCode] = useState("");
  const [capitalCity, setCapitalCity] = useState({});
  const [mapZoom, setMapZoom] = useState(1.4);
  const [countryCenter, setCountryCenter] = useState([]);

  const handleSelectAnswer = (id, city) => {
    setSelectedCountryCode(id);
    setSelectedCountry(ISO[id]);
    let cityObj = cities.filter(
      (obj) => obj.city === city && obj.iso2 === id
    )[0];
    setCapitalCity(cityObj);
    setMapZoom(findCountryCenter(id)[1]);
    console.log(findCountryCenter(id)[1]);
    setCountryCenter(findCountryCenter(id)[0]);
  };

  const handleResetQuestion = () => {
    setSelectedCountryCode("");
    setSelectedCountry("");
    setCapitalCity("");
    setMapZoom(1.4);
    setCountryCenter([]);
  };

  const getCoordinates = (lat, lng) => {
    const width = 1009.6727;
    const height = 665.96301;
    const equator = 458;

    //Calculates x based on longitude
    let x = width / 2 + (lng / 180) * (width / 2) - 30;

    //Calculates y based on latitude
    let y =
      equator -
      (lat / 90) * (height / 2) +
      (lat > 65
        ? lat * 0.15
        : lat > 55
        ? lat * 0.25
        : lat > 50
        ? lat * 0.45
        : lat > 45
        ? lat * 0.55
        : lat > 40
        ? lat * 0.65
        : lat > 35
        ? lat * 0.75
        : lat > 30
        ? lat * 0.85
        : lat > 25
        ? lat * 0.95
        : lat > 15
        ? lat * 1.2
        : lat > 7.5
        ? lat * 1.3
        : lat > 0
        ? lat * 1.6
        : lat > -20
        ? lat * -0.2
        : lat * 0.55);

    return [x, y];
  };

  return (
    <div className="App">
      <World mapZoom={mapZoom} countryCenter={countryCenter}>
        {paths.map((path, index) => {
          return (
            <Path
              path={path}
              key={index}
              selectedCountry={selectedCountry}
              selectedCountryCode={selectedCountryCode}
              className="country"
            ></Path>
          );
        })}
        {capitalCity.city && (
          <CirclePath
            className="capital"
            x={getCoordinates(capitalCity.lat, capitalCity.lng)[0]}
            y={getCoordinates(capitalCity.lat, capitalCity.lng)[1]}
            key={3000}
          />
        )}
      </World>
      {selectedCountry !== "" && (
        <Answer
          selectedCountry={selectedCountry}
          capitalCity={capitalCity}
          selectedCountryCode={selectedCountryCode}
        />
      )}
      <Question
        handleSelectAnswer={handleSelectAnswer}
        handleResetQuestion={handleResetQuestion}
      />
    </div>
  );
}

export default App;
