const Path = (props) => {
  return (
    <path
      strokeWidth="8"
      d={props.path.d}
      className={props.path.className}
      name={props.path.name}
      id={props.path.id}
      fill={props.selectedCountryCode === props.path.id ? "#50950d" : "#c4f099"}
    ></path>
  );
};

export default Path;
